/* Colors */

#links .link:hover {
    background-color: hsl(300, 30%, 30%);
}


/* Layout */

.top-bar {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.top-bar h1 {
    flex-grow: 1;
    margin-top: 0;
    margin-bottom: 0;
}

#links {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 100%;
}

#links .link {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 100%;
}


/* Dimensions */

.top-bar {
    height: 42px;
}

.top-bar .logo {
    height: 20px;
}

.top-bar > *:first-child,
.top-bar > *:last-child .link {
    padding: 0 13px;
}

.top-bar > .logo {
    margin-right: 13px;
    margin-left: 13px;
    padding: 0;
}


/* Text styles */

.top-bar h1 {
    font-size: 22px;
    font-weight: normal;
}

#links .link {
    display: flex;
    flex-direction: row;
    align-items: center;
    text-decoration: none !important;
}


/* Transitions */

.top-bar .logo,
.top-bar h1 {
    transition: opacity 200ms;
}

.top-bar:not(.root) .logo,
.top-bar:not(.root) h1 {
    opacity: 0;
}

#links .link {
    transition: background-color 300ms;
}
