body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Layout */

body {
  margin: 0;
}

#content {
  height: 100vh;
}

#ui {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
}

#map {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 0;
}

#content.embed .top-bar {
  display: none;
}

#content:not(.embed) .leaflet-top {
  top: 42px;
}

@media (max-width: 656px) {
  h1 {
    flex-grow: 1;
  }

  #description {
    display: none;
  }

  .discover + * {
    display: none;
  }
}


@media (min-width: 657px) {
  #description {
    flex-grow: 1;
    margin: 0;
    position: relative;
  }
}


/* Font styles */

body {
  font-family: 'Titillium Web', sans-serif;
}

/* Colors */

h1 {
  color: white;
}

#description, #links .link {
  color: hsl(250, 10%, 88%) !important;
}

.top-bar,
.overlay {
  background: linear-gradient(to right, rgba(56, 46, 53, 1), rgba(115, 106, 172, 0.7) 370px);
  color: white;
}

.fade-enter {
    opacity: 0;
}

.fade-enter.fade-enter-active,
.fade-exit {
    opacity: 1;
    transition: opacity 300ms ease-in;
}

#about .attribution,
#about .date-line {
    text-align: right;
}

#about .attribution {
    margin-top: 2em;
}

#about .attribution .license {
    font-size: 70%;
}

/* Colors */

.overlay,
.overlay a,
.overlay a:link,
.overlay a:visited,
.overlay a:hover {
    color: white;
}

/* Text styles */

.overlay .ui .link,
.overlay .ui .link:link,
.overlay .ui .link:visited,
.overlay .ui .link:hover {
    text-decoration: none;
}


/* Transitions */

.overlay .link.close {
    transition: background-color 300ms, opacity 300ms;
}

.overlay .link.close:not(:hover) {
    opacity: 0.5;
}

.overlay .link.close:hover {
    background-color: hsl(300, 20%, 30%);
}


/* Layout */

.overlay .link.close {
    position: fixed;
    text-align: center;
}


/* Dimensions */

.overlay {
    padding: 2em 3em;
}

@media (min-width: 657px) {
    .overlay {
        padding-left: 33%;
        padding-right: 5em;
    }
}

.overlay h2 {
    margin-top: 1.66em;
}

.overlay .link.close {
    right: 0.5em;
    top: 0.5em;
    width: 2em;
    height: 2em;
    line-height: 2em;
}


/* Fanciness */

.link.close {
    border-radius: 50%;
}

.loader {
    position: relative;
    margin: 0 auto;
    width: 100px;
}

.loader:before {
    content: '';
    display: block;
    padding-top: 100%;
}

.loader .spinner {
    -webkit-animation: spinnerRotate 2s linear infinite;
    animation: spinnerRotate 2s linear infinite;
    height: 100%;
    -webkit-transform-origin: center center;
    transform-origin: center center;
    width: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
}

.loader .path {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
    -webkit-animation: dash 1.5s ease-in-out infinite, color 6s ease-in-out infinite;
    animation: spinnerDash 1.5s ease-in-out infinite, spinnerColor 6s ease-in-out infinite;
    stroke-linecap: round;
}

@-webkit-keyframes spinnerRotate {
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@keyframes spinnerRotate {
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@-webkit-keyframes spinnerDash {
    0% {
        stroke-dasharray: 1, 200;
        stroke-dashoffset: 0;
    }
    50% {
        stroke-dasharray: 89, 200;
        stroke-dashoffset: -35;
    }
    100% {
        stroke-dasharray: 89, 200;
        stroke-dashoffset: -124;
    }
}

@keyframes spinnerDash {
    0% {
        stroke-dasharray: 1, 200;
        stroke-dashoffset: 0;
    }
    50% {
        stroke-dasharray: 89, 200;
        stroke-dashoffset: -35;
    }
    100% {
        stroke-dasharray: 89, 200;
        stroke-dashoffset: -124;
    }
}

@-webkit-keyframes spinnerColor {
    100%, 0% {
        stroke: #d62d20;
    }
    40% {
        stroke: #0057e7;
    }
    66% {
        stroke: #008744;
    }
    80%, 90% {
        stroke: #ffa700;
    }
}

@keyframes spinnerColor {
    100%, 0% {
        stroke: #d62d20;
    }
    40% {
        stroke: #0057e7;
    }
    66% {
        stroke: #008744;
    }
    80%, 90% {
        stroke: #ffa700;
    }
}
.loading-screen {
    position: absolute;
    top: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    width: 100%;
}

.loading-screen > .loader {
    width: 100px;
}


.leaflet-tooltip {
    background-color: rgba(105, 96, 135, 0.8);
}

.leaflet-tooltip-left::before {
    border-left-color: rgba(105, 96, 135, 0.8);
}

.leaflet-tooltip-right::before {
    border-right-color: rgba(105, 96, 135, 0.8);
}

.leaflet-tooltip,
.leaflet-toolip:before,
.leaflet-toolip:after {
    border: 0 none;
    color: white;
}

/* Colors */

#links .link:hover {
    background-color: hsl(300, 30%, 30%);
}


/* Layout */

.top-bar {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.top-bar h1 {
    flex-grow: 1;
    margin-top: 0;
    margin-bottom: 0;
}

#links {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 100%;
}

#links .link {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 100%;
}


/* Dimensions */

.top-bar {
    height: 42px;
}

.top-bar .logo {
    height: 20px;
}

.top-bar > *:first-child,
.top-bar > *:last-child .link {
    padding: 0 13px;
}

.top-bar > .logo {
    margin-right: 13px;
    margin-left: 13px;
    padding: 0;
}


/* Text styles */

.top-bar h1 {
    font-size: 22px;
    font-weight: normal;
}

#links .link {
    display: flex;
    flex-direction: row;
    align-items: center;
    text-decoration: none !important;
}


/* Transitions */

.top-bar .logo,
.top-bar h1 {
    transition: opacity 200ms;
}

.top-bar:not(.root) .logo,
.top-bar:not(.root) h1 {
    opacity: 0;
}

#links .link {
    transition: background-color 300ms;
}

