/* Colors */

.overlay,
.overlay a,
.overlay a:link,
.overlay a:visited,
.overlay a:hover {
    color: white;
}

/* Text styles */

.overlay .ui .link,
.overlay .ui .link:link,
.overlay .ui .link:visited,
.overlay .ui .link:hover {
    text-decoration: none;
}


/* Transitions */

.overlay .link.close {
    transition: background-color 300ms, opacity 300ms;
}

.overlay .link.close:not(:hover) {
    opacity: 0.5;
}

.overlay .link.close:hover {
    background-color: hsl(300, 20%, 30%);
}


/* Layout */

.overlay .link.close {
    position: fixed;
    text-align: center;
}


/* Dimensions */

.overlay {
    padding: 2em 3em;
}

@media (min-width: 657px) {
    .overlay {
        padding-left: 33%;
        padding-right: 5em;
    }
}

.overlay h2 {
    margin-top: 1.66em;
}

.overlay .link.close {
    right: 0.5em;
    top: 0.5em;
    width: 2em;
    height: 2em;
    line-height: 2em;
}


/* Fanciness */

.link.close {
    border-radius: 50%;
}
