.leaflet-tooltip {
    background-color: rgba(105, 96, 135, 0.8);
}

.leaflet-tooltip-left::before {
    border-left-color: rgba(105, 96, 135, 0.8);
}

.leaflet-tooltip-right::before {
    border-right-color: rgba(105, 96, 135, 0.8);
}

.leaflet-tooltip,
.leaflet-toolip:before,
.leaflet-toolip:after {
    border: 0 none;
    color: white;
}
