/* Layout */

body {
  margin: 0;
}

#content {
  height: 100vh;
}

#ui {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
}

#map {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 0;
}

#content.embed .top-bar {
  display: none;
}

#content:not(.embed) .leaflet-top {
  top: 42px;
}

@media (max-width: 656px) {
  h1 {
    flex-grow: 1;
  }

  #description {
    display: none;
  }

  .discover + * {
    display: none;
  }
}


@media (min-width: 657px) {
  #description {
    flex-grow: 1;
    margin: 0;
    position: relative;
  }
}


/* Font styles */

body {
  font-family: 'Titillium Web', sans-serif;
}

/* Colors */

h1 {
  color: white;
}

#description, #links .link {
  color: hsl(250, 10%, 88%) !important;
}

.top-bar,
.overlay {
  background: linear-gradient(to right, rgba(56, 46, 53, 1), rgba(115, 106, 172, 0.7) 370px);
  color: white;
}
